<template>
    <baidu-map class="map" :center="markerPoint" :zoom="15">
        <bm-marker :position="markerPoint" :dragging="true" @click="infoWindowOpen">
            <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">许昌市 东城区 阳光花墅 13号楼 1单元 501</bm-info-window>
        </bm-marker>
    </baidu-map>
</template>

<script>
    export default {
        data() {
            return {
                show: true,
                markerPoint:{lng: 113.886056, lat: 34.02798},
            }
        },
        methods: {
            infoWindowClose() {
                this.show = false
            },
            infoWindowOpen() {
                this.show = true
            }
        }
    }
</script>
<style lang="scss" scope>
    .map {
        height: 400px;
    }
</style>


